import React from 'react';import './App.css';class App extends React.Component {	state = {		checker: true,		starter: false,		num: '',		numInput: '',		countdown: '__',		countHid: "Counter",		textResult: `press button "Generate"`,		classDisplay: 'Display',		classStarter: 'Starter',		starterText: 'Generate a Number',		date: Date().split(' ')[4]				// clock variable	};	intervalId; 				// timer variable	//----------------------------------------------------	componentDidMount() {		setInterval(this.tick, 1000)	};														// block clock	tick = () => {		this.setState({date: Date().split(' ')[4]})	};	//----------------------------------------------------	changeInputNum = (e) => {		this.setState({numInput: +e.currentTarget.value});	};	checkNumbers = () => {		this.setState({classDisplay: 'Display'});		if (this.state.num > this.state.numInput) {			this.setState({				textResult: `${this.state.numInput} - it's Small`			});		}		if (this.state.num < this.state.numInput) {			this.setState({				textResult: `${this.state.numInput} - it's Much`			});		}		if (this.state.num === this.state.numInput) {			this.setState({				textResult: 'YOU WIN *** YOU WIN *** YOU WIN',				countdown: '',				countHid: "CounterUp",				checker: true,				starter: false,				classStarter: 'StarterUp',				starterText: 'Do YOU Want Restart?'			});			clearInterval(this.intervalId);		}	};	makeNumber = () => {		this.startTimer();		this.setState({			checker: false,			countdown: 30,			num: Math.ceil(Math.random() * 100),			countHid: "Counter",			starter: true,			classDisplay: 'Display',			classStarter: 'Starter',			starterText: 'Generate a Number',			textResult: '>>> * <<<'		});	};	cleanerNumb = () => {		this.setState({			numInput: '',			classDisplay: 'Dis'		})	};	timer = () => {		this.setState({countdown: this.state.countdown - 1});		if (this.state.countdown === 0) {			this.setState({				checker: true,				starter: false,				countHid: "CounterUp",				textResult: 'You LOSE!!!',				classStarter: 'StarterUp',				starterText: 'Do YOU Want Restart?'			});			clearInterval(this.intervalId)		}	};// CountDowner	startTimer = () => {		this.intervalId = setInterval(() => this.timer(), 1000)		};	render() {		return (			<div className="App">				<header className="App-header">					<p>{this.state.date}</p>					<i className={this.state.countHid} >						You have {this.state.countdown} sec.					</i>					<h1>Guess the Number</h1>					<div className="wrapper">						<button className={this.state.classStarter}								disabled={this.state.starter}								onClick={this.makeNumber} >							{this.state.starterText}						</button>						<input className="Window"							   type="number" min="0" max="100"							   disabled={this.state.checker}							   placeholder={'Enter your number'}							   value={this.state.numInput}							   onClick={this.cleanerNumb}							   onChange={this.changeInputNum}/>						<button className="Checker"								disabled={this.state.checker}								onClick={this.checkNumbers} >							Check your number						</button>					</div>					<div className={this.state.classDisplay}>						{this.state.textResult}					</div>				</header>			</div>		);	}}export default App;